/*eslint-disable no-useless-constructor */
import { ConfigPage } from "@macashipo/mlib"
import { Card, CardBody } from "reactstrap"
import styled from "styled-components"
import useScript from "src/hooks/useScript"
import React from "react"
const WrapContent = styled.div`
  & > iframe {
    width: 100%;
    height: 100%;
  }
`
const WrapIframe = styled.iframe`
  width: 100%;
  height: 100%;
`
export default function Page(props) {
  console.warn("render iframe", props)
  const addXFrame = React.useRef(false)
  const statusCustomElements = useScript(
    `https://unpkg.com/@ungap/custom-elements-builtin`,
    {
      removeOnUnmount: true,
    }
  )
  // const statusXFrameBypass = useScript(`https://unpkg.com/x-frame-bypass`, {
  //   removeOnUnmount: true,
  // })

  var configPage = ConfigPage.createFromConfigPageServer(props.configPage)
  var link = configPage._config["link"]
  var bypass = configPage._config["bypass"]

  if (link) {
    if (bypass) {
      // link = "https://news.ycombinator.com/"
      if (statusCustomElements && addXFrame.current === false) {
        customElements.define(
          "x-frame-bypass",
          class extends HTMLIFrameElement {
            static get observedAttributes() {
              return ["src"]
            }
            constructor() {
              super()
            }
            attributeChangedCallback() {
              this.load(this.src)
            }
            connectedCallback() {
              this.sandbox =
                "" + this.sandbox ||
                "allow-forms allow-modals allow-pointer-lock allow-popups allow-popups-to-escape-sandbox allow-presentation allow-same-origin allow-scripts allow-top-navigation-by-user-activation" // all except allow-top-navigation
            }
            load(url, options) {
              if (!url || !url.startsWith("http"))
                throw new Error(
                  `X-Frame-Bypass src ${url} does not start with http(s)://`
                )
              console.log("X-Frame-Bypass loading:", url)
              this.srcdoc = `<html>
        <head>
          <style>
          .loader {
            position: absolute;
            top: calc(50% - 25px);
            left: calc(50% - 25px);
            width: 50px;
            height: 50px;
            background-color: #333;
            border-radius: 50%;  
            animation: loader 1s infinite ease-in-out;
          }
          @keyframes loader {
            0% {
            transform: scale(0);
            }
            100% {
            transform: scale(1);
            opacity: 0;
            }
          }
          </style>
        </head>
        <body>
          <div class="loader"></div>
        </body>
        </html>`
              this.fetchProxy(url, options, 0)
                .then(res => res.text())
                .then(data => {
                  if (data)
                    this.srcdoc = data.replace(
                      /<head([^>]*)>/i,
                      `<head$1>
          <base href="${url}">
          <script>
          // X-Frame-Bypass navigation event handlers
          document.addEventListener('click', e => {
            if (frameElement && document.activeElement && document.activeElement.href) {
              e.preventDefault()
              frameElement.load(document.activeElement.href)
            }
          })
          document.addEventListener('submit', e => {
            if (frameElement && document.activeElement && document.activeElement.form && document.activeElement.form.action) {
              e.preventDefault()
              if (document.activeElement.form.method === 'post')
                frameElement.load(document.activeElement.form.action, {method: 'post', body: new FormData(document.activeElement.form)})
              else
                frameElement.load(document.activeElement.form.action + '?' + new URLSearchParams(new FormData(document.activeElement.form)))
            }
          })
          </script>`
                    )
                })
                .catch(e => console.error("Cannot load X-Frame-Bypass:", e))
            }
            fetchProxy(url, options, i) {
              const proxies = (options || {}).proxies || [
                "https://cors-anywhere.herokuapp.com/",
                "https://yacdn.org/proxy/",
                "https://api.codetabs.com/v1/proxy/?quest=",
              ]
              return fetch(proxies[i] + url, options)
                .then(res => {
                  if (!res.ok)
                    throw new Error(`${res.status} ${res.statusText}`)
                  return res
                })
                .catch(error => {
                  if (i === proxies.length - 1) throw error
                  return this.fetchProxy(url, options, i + 1)
                })
            }
          },
          { extends: "iframe" }
        )
        addXFrame.current = true
      }
      if (statusCustomElements === "ready") {
        return (
          <WrapContent className="app-main__inner_fixcontent">
            <WrapIframe is="x-frame-bypass" src={link} />
          </WrapContent>
        )
      } else {
        return (
          <Card
            className="col-12 col-sm-8 col-xl-6"
            style={{ margin: "0px auto" }}
          >
            <CardBody className="">Loading...</CardBody>
          </Card>
        )
      }
    } else {
      return (
        <WrapContent className="app-main__inner_fixcontent">
          <WrapIframe src={link} />
        </WrapContent>
      )
    }
  }
  return (
    <Card className="col-12 col-sm-8 col-xl-6" style={{ margin: "0px auto" }}>
      <CardBody className="">Missing config link</CardBody>
    </Card>
  )
}
